import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import ProjectNav from "../components/projectNav"

import TwoCols from "../components/layout/twoCols"
import BreadCrumbs from "../components/layout/breadCrumbs"
import Title from "../components/layout/title"

export default function ProjectTemplate({ pageContext: project }) {
  const prodDev = project.product_development

  const designGuides = project.guides.filter(
    guide => guide.Category === "design"
  )
  const codeGuides = project.guides.filter(guide => guide.Category === "code")

  return (
    <Layout>
      <SEO title={`Project - ${project.Title}`} />

      <div>
        <BreadCrumbs>
          {[<Link to={`/projects`}>‚Üê All projects</Link>, project.Title]}
        </BreadCrumbs>

        <Title>{project.Title}</Title>

        <TwoCols>
          <ProjectNav project={project} />

          <div style={{ maxWidth: "900px" }}>
            <section>
              <h3>Description</h3>
              <div dangerouslySetInnerHTML={{ __html: project.Description }} />
            </section>

            <TwoCols>
              {designGuides.length > 0 && (
                <div>
                  <h3>Design Research Module</h3>
                  <ul>
                    {designGuides.map((guide, key) => (
                      <li key={key}>
                        <Link to={`/guides/${guide.UID}`}>{guide.Title}</Link>
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {codeGuides.length > 0 && (
                <div>
                  <h3>Code Research Module</h3>
                  <ul>
                    {codeGuides.map((guide, key) => (
                      <li key={key}>
                        <Link to={`/guides/${guide.UID}`}>{guide.Title}</Link>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </TwoCols>

            <section>
              {prodDev && (
                <div>
                  <h3>Product Development</h3>
                  <span>{prodDev.Title}</span>
                  <br />
                  <Link to={`/product-development/${prodDev.UID}`}>
                    Go to specification
                  </Link>
                </div>
              )}
            </section>
          </div>
        </TwoCols>
      </div>
    </Layout>
  )
}
