import React from "react"
import styled from "styled-components"

import BrandStrong from "../components/layout/brandStrong"

const StyledAside = styled.aside`
  order: 2;
`

export default function projectNav({ project }) {
  const prodDev = project.product_development

  const learningGoals = {
    skills: project.guides.reduce((allSkills, guide) => {
      return [...allSkills, ...guide.Skills.map(i => i.Skill)]
    }, []),
    knowledge: project.guides.reduce((allKnowledge, guide) => {
      return [...allKnowledge, ...guide.Knowledge.map(i => i.Knowledge)]
    }, []),
    competences:
      (prodDev &&
        prodDev.Competences &&
        prodDev.Competences.map(i => i.Competence)) ||
      [],
  }

  return (
    <>
      {learningGoals.knowledge.length +
        learningGoals.knowledge.length +
        learningGoals.knowledge.length >
        0 && (
        <StyledAside>
          <section>
            <h3>Learning Goals</h3>

            {learningGoals.knowledge.length > 0 && (
              <div>
                <BrandStrong>Knowledge</BrandStrong>
                <ul>
                  {learningGoals.knowledge.map((knowledge, key) => (
                    <li key={key}>{knowledge}</li>
                  ))}
                </ul>
              </div>
            )}

            {learningGoals.skills.length > 0 && (
              <div>
                <BrandStrong>Skills</BrandStrong>
                <ul>
                  {learningGoals.skills.map((skill, key) => (
                    <li key={key}>{skill}</li>
                  ))}
                </ul>
              </div>
            )}

            {learningGoals.competences.length > 0 && (
              <div>
                <BrandStrong>Competences</BrandStrong>
                <ul>
                  {learningGoals.competences.map((competence, key) => (
                    <li key={key}>{competence}</li>
                  ))}
                </ul>
              </div>
            )}
          </section>
        </StyledAside>
      )}
    </>
  )
}
